.slider__img {
    width: 100%;
    border-radius: 4px;
    box-shadow: var(--main-shadow);
    display: block;
    margin: 0 auto;
  }
.slider__imgContainer{
    box-shadow: 10px 10px 8px black;
  }
  .slick-dots {
    display: block;
    list-style: none;
    position: absolute;
    text-align: right;
    bottom: 20px;
    margin: 0px;
    padding: 0px;
    right: 8px;
    width: 100%;
  }
  .slider__container {
    padding: 5px;
    position: relative;
    outline: none !important;
  }
  .imageSlider .slick-track,
  .imageSlider .slick-list {
    overflow: unset;
  }
  .imageSlider .slick-list {
    margin: 0 -10px;
  }
  .imageSlider .slick-dots li button::before {
    color: rgb(150, 158, 171);
    left: 0px;
    top: 0px;
    opacity: 1;
    content: "â€¢";
    width: 20px;
    height: 20px;
    font-size: 12px;
    line-height: 20px;
    position: absolute;
    text-align: center;
    -webkit-font-smoothing: antialiased;
  }
  .imageSlider .slick-dots li.slick-active button::before {
    color: rgb(249, 249, 249);
  }
  .slider__textImage {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
    width: 100%;
    height: 100%;
    opacity: 0;
    transform: translateX(15px);
    transition: transform 400ms cubic-bezier(0.25, 0.46, 0.45, 0.94) 100ms;
  }
  .slick-slide:not(.slick-cloned).slick-active .slider__textImage {
    opacity: 1;
    transform: translateX(0px);
    transition: transform 400ms cubic-bezier(0.25, 0.46, 0.45, 0.94) 420ms,
      opacity 950ms cubic-bezier(0.25, 0.46, 0.45, 0.94) 420ms;
  }
  @media (min-width: 768px) {
    .slider__container {
      padding: 10px;
    }
  }

@media only screen and (max-width: 480px){
    .slider__img{
        height: 20vh;
    }   
}

@media only screen and (min-width: 500px) and (max-width : 760px){
  .slider__img{
      height: 40vh;
  }   
}

